.navbar {
	width: 100%;
	background: grey;
	margin-bottom: 10px;
	border-bottom: 2px solid black;
}

li {
	display: inline-block;
	margin: 20px;
}

.navbar-link {
	text-decoration: none;
	color: black;
	font-size: 20px;
}

.navbar-link:hover {
	cursor: pointer;
	color: white;
}



